/**
 * Code Separator Block - Editor Styles
 */

.wp-block-eightyfourem-code-separator {
	margin: 2rem 0;
}

.wp-block-eightyfourem-code-separator .code-separator-inner {
	display: flex;
	align-items: center;
	justify-content: center;
}

.wp-block-eightyfourem-code-separator .code-separator-icon {
	width: 30px;
	height: 30px;
	margin: 0 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.wp-block-eightyfourem-code-separator .code-separator-icon svg {
	width: 100%;
	height: 100%;
	fill: currentColor;
}

.wp-block-eightyfourem-code-separator .code-separator-inner::before,
.wp-block-eightyfourem-code-separator .code-separator-inner::after {
	content: '';
	flex-grow: 1;
	height: 0;
	border-top: 1px solid #333;
}
